<template>
    <div class="todo-item">
        <div class="todo-item-left">
        <input type="checkbox" v-model="completed" @change="doneEdit">
        <div v-if="!editing" 
        @dblclick="editTodo" class="todo-item-label" :class="{ completed : completed }">{{ title }}</div>
        <input v-else class="todo-item-edit" type="text" v-model="title" 
        @blur="doneEdit" @keyup.enter="doneEdit" @keyup.esc="cancelEdit" v-focus>
      </div>
      <div class="remove-item" @click="removeTodo(index)">
        &times;
      </div>
    </div>
</template>

<script>
export default {
    name: 'tood-item',
    props: {
        todo: {
            type: Object,
            required: true,
        },
        index: {
            type: Number,
            required: true,
        },
        checkAll: {
            type: Boolean,
            required: true
        }
    },

    //Our data
    data(){
        return {
            'id': this.todo.id,
            'title': this.todo.title,
            'completed': this.todo.completed,
            'editing': this.todo.editing,
            'beforeEditCache': '',
        }
    },

    //Watcher to watch props and when they change
    //This will solve a "Check All" issue not showing items left after checking All or unchecking
    watch: {
        checkAll() {
            this.completed = this.checkAll ? true : this.todo.completed;
        }
    },

    //will focus when double clicking on a task/todo item
    directives: {
        focus: {
            inserted: function (el) {
                el.focus()
            }
        }
    },

    //Our methods because duh obviously
    methods: {
        removeTodo(index){
            this.$eventBus.$emit('removedTodo', index);
        },
        editTodo() {
            this.beforeEditCache = this.title
            this.editing = true
        },
        doneEdit() {
        if (this.title.trim() == '') {
            this.title = this.beforeEditCache
        }
            this.editing = false
            this.$eventBus.$emit('finishedEdit', {
                'index': this.index,
                'todo': {
                    'id': this.id,
                    'title': this.title,
                    'completed': this.completed,
                    'editing': this.editing,
                }
            })
        },
        cancelEdit() {
            this.title = this.beforeEditCache
            this.editing = false
        },
    }
}

</script>

