<template>
  <div id="register">
    <div class="register-form">
      <div v-if="error" class="alert alert-danger">{{error}}</div>
      <form action="#" @submit.prevent="submit">
        <div class="input">
          <label for="name">Name</label>
          <input
                  type="name"
                  id="name"
                  class="form-control"
                  name="name"
                  placeholder="Name..."
                  value
                  required
                  autofocus
                  v-model="form.name">
        </div>
        <div class="input">
          <label for="email">Mail</label>
          <input
                  type="email"
                  id="email"
                  class="form-control"
                  name="email"
                  placeholder="Mail..."
                  value
                  required
                  autofocus
                  v-model="form.email">
        </div>
        <div class="input">
          <label for="password">Password</label>
          <input
                  type="password"
                  id="password"
                  class="form-control"
                  name="password"
                  placeholder="Password..."
                  required
                  v-model="form.password" />
        </div>
        <div class="submit">
          <button type="submit">Register</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  data() {
    return {
      form: {
        name: "",
        email: "",
        password: ""
      },
      error: null
    };
  },
  methods: {
    submit() {
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.form.email, this.form.password)
        .then(data => {
          data.user
            .updateProfile({
              displayName: this.form.name
            })
            .then(() => {
              this.$router.replace({ name: "dashboard" });
            });
        })
        .catch(err => {
          this.error = err.message;
        });
    }
  }
};
</script>

