import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({

  //Information about user through our user object
  state: {
    user: {
      loggedIn: false,
      data: null
    }
  },

  //Through mutations state can be changed
  mutations: {
    SET_LOGGED_IN(state, value) {
      state.user.loggedIn = value;
    },
    SET_USER(state, data) {
      state.user.data = data;
    }
  },

  //Commiting mutations through actions
  actions: {
    fetchUser({ commit }, user) {
      commit("SET_LOGGED_IN", user !== null);
      if (user) {
        commit("SET_USER", {
          displayName: user.displayName,
          email: user.email
        });
      }
      else {
        commit("SET_USER", null);
      }
    }
  },

  //returns user object from state
  getters: {
    user(state) {
      return state.user;
    }
  }
})